body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #111;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 2rem;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  text-align: center;
}

h1 {
  font-size: 3.9rem;
  font-weight: 900;
}

h2 {
  font-size: 3.5rem;
  font-weight: 700;
}

h3 {
  font-size: 3.1rem;
  font-weight: bold;
}

h4 {
  font-size: 2.5rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #ddc904;
  color: #111;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0rem;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c4b204;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #ddc904;
  color: #111;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0rem;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c4b204;
  cursor: pointer;
}

.boton-verde {
  background-color: #577d0e;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0rem;
  border: none;
}
.boton-verde:hover {
  background-color: #47660b;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #577d0e;
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0rem;
  border: none;
}
.boton-verde-block:hover {
  background-color: #47660b;
  cursor: pointer;
}

.dark-mode {
  background-color: #111;
}
.dark-mode p, .dark-mode a {
  color: #fff;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode blockquote {
  color: #dddddd;
}
.dark-mode .anuncio {
  background-color: #373737;
}
.dark-mode .anuncio .precio {
  color: #76ab13;
}
.dark-mode .iconos-caracteristicas img {
  filter: invert(1);
}
.dark-mode .formulario {
  color: #dddddd;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #373737;
  border-color: #dddddd;
  color: #fff;
}

.inicio {
  background-image: url(/build/img/header.webp);
}

.header {
  background-color: #373737;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  min-height: 100vh;
}
.header.inicio .contenido-header {
  min-height: calc(100vh - 5.5rem);
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #fff;
  line-height: 2;
  max-width: 60rem;
}

.barra {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.barra .logo {
  grid-column: 1/11;
}
.barra .mobil-menu {
  grid-column: 12/13;
}
.barra .mobil-menu img {
  margin: auto 0;
}
.barra .navegacion {
  grid-column: 10/13;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out, height 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  color: #fff;
  display: block;
  text-align: center;
}
.navegacion img {
  width: 3rem;
  filter: invert(1);
}
.navegacion img:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .mobil-menu {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: 17rem;
}

@media (min-width: 1024px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #dddddd;
  border: 1px solid #373737;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.anuncio:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 1024px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #577d0e;
  font-weight: 900;
  font-size: 3.1rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.imagen-contacto {
  background-image: url(/build/img/encuentra.webp);
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 3.9rem;
  color: #fff;
}
.imagen-contacto p {
  color: #fff;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 4rem;
  }
}

.entrada-blog {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 1rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #111;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.5;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin-top: 0;
  }
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #76ab13;
  margin-top: 1rem;
}
.texto-entrada p {
  margin: 1rem 0;
}
.texto-entrada span {
  color: #928503;
}

.testimonial {
  background-color: #577d0e;
  font-size: 2.5rem;
  padding: 2rem;
  color: #fff;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  height: 8rem;
  width: 6rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #fff;
}

.footer {
  background-color: #373737;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #fff;
  text-align: center;
}

.formulario {
  font-size: 1.6rem;
  color: #373737;
}
.formulario legend {
  font-size: 2rem;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 0.5rem;
  display: block;
  width: 100%;
  background-color: #fff;
  border: 1px solid #373737;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario p {
  margin: 2rem 0 0 0;
}
.formulario p:first-of-type {
  margin: 0;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .contenido-nosotros img {
    margin-top: 8rem;
  }
}

.texto-nosotros blockquote {
  font-size: 2.5rem;
  font-weight: 900;
  margin: 3rem 0 0 0;
  padding: 1rem 0 2rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 0.5rem;
}
.alerta.error {
  background-color: #ddc904;
}
.alerta.exito {
  background-color: #76ab13;
}

.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
.propiedades thead {
  background-color: #76ab13;
}
.propiedades thead th {
  color: #fff;
  padding: 2rem;
}
.propiedades .imagen-tabla {
  width: 10rem;
}

.formulario .imagen-small {
  max-width: 20rem;
}